.footer {
    margin-top: 10px;

    background: #5FAD56;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;


    .title {
        width: 100%;
        color: #FFF;
        font-family: 'Lobster', sans-serif;
        text-decoration: none;
        font-size: 2rem;
        text-align: center;
        margin-bottom: 20px;
    }

    .container {
        width: 1100px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin-bottom: 10px;
        border: 1px solid black;

        .col {
            width: 30%;
            height: 150px;
            text-align: center;
            background: rgb(155, 212, 150);
            padding: 10px 0;
            margin: 0 50px;
            border-radius: 5px;

            ul {
                li {
                    text-decoration: none;
                    list-style: none;
                    padding: 10px 0;
                }
            }
        }
    }

    .copyright {
        font-family: 'Lobster', sans-serif;
    }
}

//Responsive

@media(max-width:1100px) {

    .container {
        .col {
            width:10%;
            margin: 0;
            padding:0;
        }
    }

}

@media(max-width:960px) {
    .container{
        width:960px!important;
        flex-direction:column!important;

    .col{
        
    }
    }
}